.shipping-container {
    max-width: 1200px;
    margin-inline: auto;
    display: flex;
    justify-content: center;
    padding: 1em;
    margin-top: 1rem;
    margin-bottom: 2rem;
    height: max-content;
}

.shipping-header {
    text-align: center;
    font-family: var(--ff-primary);
    font-weight: var(--fw-reg);
    font-size: var(--fs-h2);
    padding: .3em;
    border-bottom: 2px solid var(--clr-sunset-orange);
}

.shipping-box {
    font-family: var(--ff-secondary);
    font-weight: var(--fw-reg);
    font-size: var(--fs-h3);
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: .5em;
    height: 500px;
    box-shadow: var(--bs);
}

.shipping-form {
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 1em;
    padding-top: 0;
    margin-top: 0;
    height: 100%;
    justify-content: space-evenly;
}

.address-name,
.address-city,
.address-pincode,
.address-phoneno,
.address-country,
.address-state {
    display: flex;
    width: 100%;
    align-items: center;
}

.address-name>input,
.address-city>input,
.address-pincode>input,
.address-phoneno>input {
    padding: .5em 1.5em;
    padding-left: 2.5rem;
    width: 100%;
    border: 1px solid black;
    outline: none;
    font-family: var(--ff-secondary);
}

.address-country>select,
.address-state>select {
    padding: .5em 1.5em;
    padding-left: 2.5rem;
    width: 100%;
    border: 1px solid black;
    outline: none;
    font-family: var(--ff-secondary);
}

.address-name>svg,
.address-city>svg,
.address-pincode>svg,
.address-phoneno>svg,
.address-country>svg,
.address-state>svg {
    position: absolute;
    padding: .4em;
    font-weight: var(--fw-bold);
    font-size: var(--fs-body);
    opacity: .7;
}


@media (max-width: 600px) {
    .shipping-form {
        width: 100%;
    }

    .shipping-box {
        box-shadow: none;
    }
}

.error {
    font-family: var(--ff-secondary);
    font-weight: var(--fw-bold);
    font-size: var(--fs-body);
    color: #cc0000;
    text-align: center;
    margin-inline: auto;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
}

.profile-back-btn {
    display: block;
    color: var(--clr-dark);
    padding: 1em;
    padding-left: 0;
    font-family: var(--ff-primary);
    font-style: var(--fs-body);
    max-width: 1200px;
    margin-inline: auto;
    width: 100%;
    padding-left: 2em;
    text-align: left;
}