.forgot-password-container {
    max-width: 1200px;
    margin-inline: auto;
    display: flex;
    justify-content: center;
    padding: 1em;
    margin-top: 1rem;
    margin-bottom: 2rem;
    height: max-content;
}

.forgot-password-container__header {
    text-align: center;
    font-family: var(--ff-primary);
    font-weight: var(--fw-reg);
    font-size: var(--fs-h2);
    padding: .3em;
    border-bottom: 2px solid var(--clr-sunset-orange);
}

.forgot-box {
    font-family: var(--ff-secondary);
    font-weight: var(--fw-reg);
    font-size: var(--fs-h3);
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: .5em;
    height: 350px;
    box-shadow: var(--bs);
}

.forgot-form {
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 1em;
    padding-top: 0;
    margin-top: 0;
    height: 100%;
    justify-content: space-evenly;
}

.forgot-email {
    display: flex;
    width: 100%;
    align-items: center;
}


.forgot-email>input {
    padding: .5em 1.5em;
    padding-left: 2.5rem;
    width: 100%;
    border: 1px solid black;
    outline: none;
    font-family: var(--ff-secondary);
}


.forgot-email>svg {
    position: absolute;
    padding: .4em;
    font-weight: var(--fw-bold);
    font-size: var(--fs-body);
    opacity: .7;
}


@media (max-width: 600px) {
    .forgot-email>svg {
        font-size: var(--fs-h3);
    }

    .forgot-form {
        width: 100%;
    }

    .forgot-box {
        box-shadow: none;
    }


    .fogot-password-container {
        height: 50vh;
    }
}

.error {
    font-family: var(--ff-secondary);
    font-weight: var(--fw-bold);
    font-size: var(--fs-body);
    color: #cc0000;
    text-align: center;
    margin-inline: auto;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
}

.message {
    font-family: var(--ff-secondary);
    font-weight: var(--fw-bold);
    font-size: var(--fs-body);
    color: green;
    text-align: center;
    margin-inline: auto;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
}

.back-btn {
    display: block;
    color: var(--clr-dark);
    padding: 1em;
    padding-left: 0;
    font-family: var(--ff-primary);
    font-style: var(--fs-body);
    max-width: 1200px;
    margin-inline: auto;
    width: 100%;
    padding-left: 2em;
    text-align: left;
    text-align: center;
}